  /**
   * to sn phm bo hnh ln DB
   * @param bill_type 
   * @returns Promise<any[]>
   * @example  this.createWarranty([<array_warranty_product>]).then(() => 
   */
  createWarranty(array) {
    const promiseAddWarranty = []
    for (let i = 0; i < array.length; i++) {
      promiseAddWarranty.push(this.vhQuerySales.addWarranty(array[i]))
    }
    return Promise.all(promiseAddWarranty)
  }


  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_warranty')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return WarrantyK57Col3Component
      case 'print_size_k80_3c': return WarrantyK80Col3Component
      case 'print_size_k80_4c': return WarrantyK80Col4Component
      case 'print_size_a5': return WarrantyA5Component
      case 'print_size_a4': return WarrantyA4Component
      default: return WarrantyA4Component
    }
  }

  /**
   * tr v trang danh sch ha n sau khi  thanh ton xong
   * @example this.handle(true)
   */
  handle() {
    this.isExit = true;
    this.router.navigate(['/sales/dashboard/manage/warranty/invoices'], { state: this.dataRestore })
  }
  /**
   * kim tra d liu thng tin my in v m component in
   * @example this.handlePrint()
   */
  handlePrint() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice, invoice_detail: this.invoiceDetail, warrantyProd: this.warrantyProd, warrantyNote: this.warrantyNote }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle())
        })
    }, (err) => this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
      .then(() => this.handle()))
  }

